<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <button
      class="pagination__link pagination__link--arrow"
      :class="{'pagination__link--disabled': page === 1}"
      aria-label="Предыдущая страница"
      @click.prevent="leftPaginate()"
      :disabled="leftDisabled"
      >

      <svg width="8" height="14" fill="currentColor">
        <use xlink:href="#icon-arrow-left"></use>
      </svg>
      </button>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click="$event.preventDefault(); paginate(pageNumber)">
        {{pageNumber}}
      </a>
    </li>
    <li class="pagination__item">
      <button
      class="pagination__link pagination__link--arrow"
      :class="{'pagination__link--disabled': page === sumPage}"
      aria-label="Следующая страница"
      @click.prevent="rightPaginate()"
      :disabled="rightDisabled"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </button>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  data() {
    return {
      sumPage: 0,
      leftDisabled: false,
      rightDisabled: false,

    }
  },
  computed: {
    pages() {
      this.sumPage = Math.ceil(this.count / this.perPage)
      return this.sumPage
    }
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page)
    },
    rightPaginate() {
      if(this.sumPage !== this.page) {
        this.$emit('paginate', this.page += 1)
        this.leftDisabled === false
      }
      else {
        this.rightDisabled === true
      }

    },
    leftPaginate() {
      if(this.page !== 1) {
        this.$emit('paginate', this.page -= 1)
        this.leftDisabled === false
      }
      else {
        this.leftDisabled === true
      }

    }
  }
}
</script>
